@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input {
    /* display: block; */
    padding: 1rem;
    border: 1px solid rgb(179, 170, 170);
    border-radius: 0.3rem 0 0 0.3rem;
}

button {
    color: ivory;
    background: linear-gradient(to right, #243B55, #141E30);
    padding: 1rem;
    border: 0;
    border: 2px solid transparent;
    border-radius: 0 0.3rem 0.3rem 0;
    text-decoration: none;
    font-weight: 600;
    font-size: 1.5rem;
}

/* two columns for the todo form */ 
.dashboard_todo__1yjcD {
    display: grid;
    grid-template-columns: 1fr auto;
}

.dashboard_todo_list__1-WN3 {
    background: white;
    padding: 3rem;
    border-radius: 1rem;
    width: 600px;
    margin: 4rem 0;
}

.dashboard_todo_list__1-WN3 ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.todoItem_todoItem__2Kliz {
    padding: 1rem 0;
    display: grid;
    grid-template-columns: auto 1fr 1fr auto;
    grid-gap: 1rem;
    align-items: center;
    border-bottom: 1px solid rgb(179, 170, 170);
}

/* .todoItem input {
    margin-right: 1rem;
} */

/* if checkbox is checked, target adjacent span */
.todoItem_todoItem__2Kliz input[type="checkbox"]:checked + span {
    opacity: 0.5;
    text-decoration: line-through;
}

.todoItem_todoItem__2Kliz button {
    border-radius: 0.3rem;
    padding: 0.5rem;
    font-size: 1rem;
    cursor: pointer;
    transition: transform 0.3s;
}

.todoItem_todoItem__2Kliz button:hover {
    transform:scale(1.2);
}
body {
  min-height: 100vh;
  display: grid;
  justify-content: center;
  background-color: #243B55;
  font-family: "Inter", sans-serif;
}


